@import "../../../data";

.wrapper {
  margin-bottom: 2em;
}

.cover {
  position: relative;
  height: 15rem;
  overflow: hidden;
  margin-bottom: 1rem;

  img {
    position: absolute;
    width: 100%;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
  }
}

.user {
  display: flex;
  margin-bottom: 2em;
}

.avaWrapper {
  position: relative;
  width: 23em;

  form {
    position: absolute;
    z-index: 1;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: $main-color;
    opacity: 0;
    transition: .2s ease-in-out;

    input {
      position: relative;
      z-index: 3;
      display: block;
      width: 100%;
      height: 100%;
      cursor: pointer;
      opacity: 0;
    }
    span {
      opacity: 0;
    }
  }

  &:hover {
    form {
      opacity: .5;

      span {
        position: absolute;
        z-index: 2;
        opacity: 1;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);

        text-align: center;
        line-height: 1;
        color: rgb(255, 255, 255);
      }
    }
  }
}
.ava {
  display: block;
}

.desc {
  //position: relative;
  width: 100%;
  padding: 0 1.5em;
}
.name {
  margin-bottom: .75rem;
  font-size: 1.25rem;
  font-weight: bold;
}
.data {
  margin-bottom: .5em;

  b {
    font-weight: 500;
  }

  p {
    margin: 0 0 .5em;
    font-size: 85%;
  }
}
.Yes {
  color: $main-color;
}
.No {
  color: $error-color;
}
.contacts {
  position: relative;
  margin-bottom: .5em;

  b {
    font-size: 85%;
    font-weight: 500;
    text-transform: capitalize;
  }
  a {
    color: $main-color;
    transition: .2s ease;
    font-weight: 500;

    &:hover {
      color: darken($main-color, 5);
    }
  }
}
.contactsTitle {
  font-weight: 500;
  font-size: 1.1em;
}

