@import "../../../../data";

.item {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  flex-wrap: wrap;
  width: 100%;
  margin-bottom: .1em;
  padding: 1em;
  box-sizing: border-box;
  background: lighten($gray-color, 19);
}

$pic_wh: 4rem;
.picWrapper {
  width: $pic_wh;
  text-align: center;
}
.pic {
  width: $pic_wh;
  height: $pic_wh;
  object-fit: cover;
  border-radius: 50%;
}
.ContentWrapper {
  width: calc(100% - #{$pic_wh});
  padding-left: 1rem;
  box-sizing: border-box;
}
.title {
  margin: 0 0 .15em;
  font-size: 1.25em;
  font-weight: bold;
}
.text {
}
