.Status {
  position: absolute;
  z-index: 2;
  top: 0;
  right: 0;
  width: auto;
  max-width: 20em;
  padding: 0;
  border-radius: .2em;
  transition: all .2s ease-in-out;

  &:hover,
  &.active {
    padding: 1em;
    background: #f0f0f0;
    cursor: pointer;
    box-shadow: -1px 1px 3px #ccc;
  }
  &.active {
    width: 100%;
  }
}
.Text {
  font-size: .85em;
}
.Pencil {
  position: absolute;
  top: .25em;
  right: .25em;
  width: .75em;
  height: .75em;
  opacity: .25;
}

.Form {
  width: 100%;
}

.Input {
  width: 100%;
  padding: .5em;
  border-radius: .2em;
  border: 0;
  box-shadow: 0 0 0 1px #e0e0e0;
  background: rgb(247, 247, 247);
  outline: none;
  box-sizing: border-box;

  &:focus {
    box-shadow: 0 0 0 1px #009e6b;
    background: #fff;
  }
}
