@import '../../../data';

.Form {

}
.Row {
  margin: 0 0 1em;

  &.TextRight {
    text-align: right;
  }
}
.Label {
  display: block;
  margin-bottom: .35em;
  font-size: .9em;
}
.Checkbox {
  margin-right: .35em;
}
.CheckboxLabel {
  margin-bottom: .25em;
  font-size: .9em;
}
.Input {
  width: 100%;
  padding: .75em;
  box-sizing: border-box;
  border: 0;
  border-radius: .25em;
  box-shadow: 0 0 0 1px $gray-color inset;
  outline: none;
}
.Textarea {
  width: 100%;
  padding: .75em;
  box-sizing: border-box;
  border: 0;
  border-radius: .25em;
  box-shadow: 0 0 0 1px $gray-color inset;
  outline: none;
  resize: none;
}
.Button {
  padding: .5em 1em;
  border: 0;
  border-radius: 3px;
  color: rgb(255, 255, 255);
  background: $main-color;
  cursor: pointer;
  outline: none;

  &:hover {
    background: darken($main-color, 10);
  }
  &[disabled] {
    opacity: .3;
    cursor: default;
  }
}
.Error {
  font-size: 80%;
  color: $error-color;
}
.ErrorIcon {
  margin-left: .5em;
  width: 1.2em;
  height: 1.2em;
  fill: $error-color;
}
.FieldError {
  position: relative;

  .Input, .Textarea {
    box-shadow: 0 0 0 1px $error-color inset;
  }

  &:after {
    content: '';
    position: absolute;
    display: block;
    top: calc(50% - .75em);
    right: .25em;
    width: 1.5em;
    height: 1.5em;
    background: $error-icon-url;
  }
}
.Warning {
  font-size: 80%;
  color: $warning-color;
}
.FieldWarning {
  position: relative;

  .Input, .Textarea {
    box-shadow: 0 0 0 1px $warning-color, inset 0 0 0 1px $warning-color !important;
  }

  &:after {
    content: '';
    position: absolute;
    display: block;
    top: calc(50% - .75em);
    right: .25em;
    width: 1.5em;
    height: 1.5em;
    background: $warning-icon-url;
  }
}
.Hidden {
  display: none;
}
